
import LoadMore from 'vux/src/components/load-more/index.vue'

import commonTab from '@/components/commonTab'
import courseCard from '@/components/courseCard'
export default {
name: 'myCollect',
components:{
commonTab,courseCard,LoadMore
},
data(){
return{
listStatus:false,
TabList: [
{name: '官方课程',value:'0'},
{name: '非官方课程',value:'1'}
],
list: [],
allLoaded: false,
bottomStatus: '',
}
},
methods:{
getTabIndex(type){
console.log(type)
},
handleBottomChange(status) {
console.log(status)
this.bottomStatus = status;
},

loadBottom() {
setTimeout(() => {
let lastValue = this.list[this.list.length - 1];
if (lastValue < 40) {
for (let i = 1; i <= 10; i++) {
this.list.push(lastValue + i);
}
} else {
this.allLoaded = true;
}
this.$refs.loadmore.onBottomLoaded();
}, 5000);
}
},
created() {
for (let i = 1; i <= 10; i++) {
this.list.push(i);
}
}
}



